body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  background-color: rgb(216, 216, 216);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100vh;
  margin-top: 0;
}
.App {
  text-align: center;
}

.App_Components {
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
}

.CartActive {
  display: grid;
  grid-template-columns: 0 60% auto;
}

.CartActive > .Main {
  display: grid;
  grid-template-columns: 300px 300px;
  grid-row-gap: 0;
  height: auto;
  -webkit-transition: 2s;
  transition: 2s;
}

header {
  color: white;
  background-color: rgb(48, 48, 48);
  font-size: 40px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

header span {
  margin-left: 40%;
  text-shadow: 5px 5px 5px rgb(0, 0, 0);
}

.fa-shopping-cart {
  margin-left: 30%; 
  cursor: pointer;
  color: rgb(255, 181, 43);
  text-shadow: 10px 5px 1px rgb(0, 0, 0);
}
.SideBar {
  background-color: rgb(255, 173, 21);
  height: auto;
  border: solid 1px black;
}

.SideBar button {
  display: grid;
  grid-template-rows: 20px 20px 20px 20px 20px 20px;
  width: 50px;
  height: 50px;
  margin-left: 30px;
  margin-bottom: 2vh;
  background-color: rgb(255, 255, 255);
  border: solid 2px black;
  border-radius: 50%;
}

button:focus {
  border: solid 2px white;
  font-weight: bold;
  background-color: black;
  color: white;
}

.Main {
  display: grid;
  background-color: rgb(196, 196, 196);
  height: 100%;
  grid-template-columns: 30% 30% 3%;
  grid-template-rows: 30% 30% 30%;
  grid-column-gap: 35px;
  -webkit-column-gap: 35px;
          column-gap: 35px;
  grid-row-gap: 35px;
  row-gap: 35px;
  width: 100%;
  -webkit-transition: 2s;
  transition: 2s;
}

.Main img {
  width: 300px;
  height: 300px;
}

.shirtImage:hover {
  cursor: pointer;
  border: 1px solid white;
  box-shadow: 1px 1px 1px 0px white;
}

.shirtImage {
  margin-left: 40px;
  margin-top: 10px;
  width: 300px;
  height: 350px;
}

.addToCart {
  width: 150px;
  margin-left: 24%;
  height: 40px;
  padding-top: 3%;
  color: orange;
  background-color: rgb(58, 58, 58);
}

.addToCart:hover {
    background-color: orange;
    color: black;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.Cart {
    background-color: rgb(255, 173, 21);
    height: 100%;
    border-left: solid 1px black;
}

.Cart:hover {
    box-shadow: 0px 0px 10px 2px black;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.fa-shopping-bag {
    font-size: 50px;
    color: black;
    text-decoration: none;
}
